    <core-Footer/>
  </v-app>
</template>

<script>
import { mapState} from 'vuex';
export default {
  data () {
    return {
    }
  },
  created () {
    this.$router.replace({name: '1_Leaflet', meta: { userId: 123 }});
    this.fetchData();
  },
