    if(this.setRoute != undefined){
      this.$router.push(this.setRoute);
    }else{
      this.$router.push('/1_Leaflet');
    }
    // console.log(this.$router);
    // console.log(this.$route);
  },
  methods:{
    fetchData(){
      // `getPost`를 데이터 가져오기 위한 유틸리티/API 래퍼로 변경합니다.
      // console.log("this.$route.params.id",this.$route);
      // this.$route.meta.authRequired = true;
      // console.log("this.$route.params.id",this.$route.meta);
    }
  },
  computed: {
    ...mapState(['setRoute']),
  },
  watch: {
    '$route': 'fetchData'
  }
}
</script>
